        </div>`;
      this.printHtml(orderHtml)
    },
    printHtml(orderHtml) {
      var styles = '.main {width: 190mm;margin: 0 auto;background: transparent;font-size: 16px;}table {table-layout: fixed;border-collapse: collapse;border-spacing: 0px;width: 100%;}td {word-break: keep-all;overflow: hidden;white-space: nowrap;padding: 5px;border: 1px solid #000;}.com-a {display: flex;justify-content: space-between;font-size: 14px;margin: 4px 10px;}.a {font-size: 24px;text-align: center;margin: 10px 0 5px;}.a--c {font-size: 20px;text-align: center;margin: 5px 0 10px;}.c-a {width: 12%;}.c-c {width: 38%;}.c-b {float: right;}.m {font-size: 24px;text-align: center;margin: 10px 0 5px;}.o td {text-align: center;}.p {margin-top: 8mm;}.d-a {margin-top: 12px;flex: 1;}';
      var domStr = `
