        <title></title>
        <style>${styles}</style>
      </head>
      <body>${orderHtml}</body>
      </html>`
      let LODOP = getLodop();
      LODOP.PRINT_INIT("")
      LODOP.SET_PRINT_PAGESIZE(1, '241mm', '130mm', 'CreateCustomPage');
      LODOP.ADD_PRINT_HTM("2%", "7mm", "200mm", "96%", domStr);
      if (this.$apis.isDev) {
        LODOP.PRINT_DESIGN();
        // LODOP.PREVIEW();
      } else {
        LODOP.PRINT();
      }
    },
    refreshr() { // 
      for (let key in this.formIn) {
        this.formIn[key].value = ''
      }
      this.bang.weightType = ''
      this.makeSno()
      this.inVals.bulu = false
      this.inVals.saveId = ''
      this.getListPage(true)
      this.getPreData()
    },
    budar() { // 
      var order = {}
      this.printr(this.list.lastActiveRow)
    },
    bulur() { // 
      // console.log(this.inVals.bulu)
      this.makeSno()
      this.formIn.grossWeight.disabled = !this.inVals.bulu
      this.formIn.carWeight.disabled = !this.inVals.bulu
    },
    getBaseInfoByCarNo() {
      if (!this.autoGetBaseinfoByCarNo) {
        return
      }
      this.$httpd({
        url: this.$apis.getlogbycarNo,
        method: 'get',
        params: { carNumber: this.formIn.carBrand.value }
      }).then((res) => {
        // 
        // this.fillingBaseInfo(res.data)
        var originInfos = res.data
        for (let key in this.formIn) {
          if (this.formIn[key].isBase && originInfos[key]) {
            this.formIn[key].value = originInfos[key]
          }
        }
      })
    },
    computedNetWeight() { // 
      if (this.formIn.carWeight.value && this.formIn.grossWeight.value && (this.formIn.grossWeight.value - this.formIn.carWeight.value >= 0)) {
        this.formIn.netWeight.value = (this.formIn.grossWeight.value - this.formIn.carWeight.value).toFixed(2)
      } else {
        this.formIn.netWeight.value = ''
      }
    },
    computedJiesuanNetWeight() { // 
      this.formIn.takeOff.value = (this.formIn.netWeight.value * this.formIn.takeoffRate.value / 100).toFixed(2)
      this.formIn.settleNetweight.value = (this.formIn.netWeight.value - this.formIn.takeOff.value).toFixed(2)
    },
    computedAmount() { // 
      if (this.formIn.settleNetweight.value) {
        var amount = this.formIn.settleNetweight.value * this.formIn.price.value
        this.formIn.totalFee.value = amount >= 0 ? amount.toFixed(2) : ''
      } else {
        this.formIn.totalFee.value = ''
      }
    },
    receiveList() { // 
      this.$httpd({
        url: this.$apis.receiveList,
        method: 'post',
      })
    },
